.video-gallery {

    display: grid;
    grid-auto-flow: row;
    display: grid;
    grid-auto-flow: row;
    grid-auto-rows: auto;
    grid-template-columns: repeat(3, minmax(270px, 1fr));
    grid-gap: 49px;

    @media(max-width: 1024px) {
        grid-template-columns: repeat(2, minmax(270px, 1fr));
        grid-gap: 56px;
    }

    @media(max-width: 620px) {
        grid-template-columns: repeat(1, minmax(270px, 1fr));
        grid-gap: 40px;
    }


    &__item {
        position: relative;

        // &::before {
        //     content: none !important;
        // }


        &:not(:nth-child(3n)) {
            &:after {
                position: absolute;
                content: "";
                width: 1px;
                height: 100%;
                max-height: 290px;
                background-color: $gray-3;
                right: -25px;
                top: 0px;
                bottom: 0;

                @media(max-width: 1024px) {
                    content: none;
                }
            }
        }

        &:not(:nth-child(2n)) {
            &:after {
                @media(max-width: 1024px) {
                    position: absolute;
                    content: "";
                    width: 1px;
                    height: 100%;
                    max-height: 290px;
                    background-color: $gray-3;
                    right: -25px;
                    top: 0px;
                    bottom: 0;
                }

                @media(max-width: 620px) {
                    content: none;
                }
            }
        }



        &:nth-child(3n+1) {
            &:before {
                position: absolute;
                content: "";
                width: 324%;
                left: 0%;
                top: -30px;
                height: 1px;
                background-color: $gray-3;

                @media(max-width: 1024px) {
                    content: none;
                }
            }
        }

        // &:nth-child(2n+3) {
        //     &:before {
        //         @media(max-width: 1024px) {
        //             position: absolute;
        //             content: "";
        //             width: calc(100vw - 80px);
        //             left: 0;
        //             top: -30px;
        //             height: 1px;
        //             background-color: $gray-3;
        //         }

        //         @media(max-width: 768px) {
        //             width: calc(100vw - 30px);
        //         }

        //         @media(max-width: 620px) {
        //             content: none;
        //         }
        //     }
        // }




        &:nth-child(2):before {
            @media(min-width: 621px) {
                content: none;
            }
        }

        // &:last-child {
        //     &:after {
        //         position: absolute;
        //         content: "";
        //         width: 324%;
        //         right: 0%;
        //         left: auto;
        //         bottom: -30px;
        //         height: 1px;
        //         background-color: $gray-3;

        //         @media(max-width: 1350px) {
        //             width: calc(100vw - 30px);
        //         }

        //         @media(max-width: 1024px) {
        //             width: calc(100vw - 80px);
        //         }

        //         @media(max-width: 768px) {
        //             width: calc(100vw - 30px);
        //         }

        //         @media(max-width: 620px) {
        //             width: calc(100vw - 0px);
        //             left: -15px;
        //         }
        //     }
        // }

        &:nth-child(-n+3) {
            &::before {
                content: none;
            }
        }

        &:nth-child(2n+1) {
            &:before {
                @media(max-width: 1024px) {
                    position: absolute;
                    content: "";
                    width: calc(100vw - 80px);
                    left: 0;
                    top: -30px;
                    height: 1px;
                    background-color: $gray-3;
                }

                @media(max-width: 768px) {
                    width: calc(100vw - 30px);
                }

                @media(max-width: 620px) {
                    content: none;
                }
            }
        }

        &:nth-child(1),
        &:nth-child(2) {
            &:before {
                @media(max-width: 1024px) {
                    content: none;
                }
            }
        }

        &:not(:first-child) {
            &:before {
                @media(max-width: 620px) {
                    position: absolute;
                    content: "";
                    width: calc(100vw - 0px);
                    left: -15px;
                    top: -25px;
                    height: 1px;
                    background-color: $gray-3;
                }
            }
        }

    }



    &__inner {
        position: relative;
        height: 254pz;

        @media(max-width: 1024px) {
            height: 210px;
        }

        @media(max-width: 620px) {
            height: 220px;
        }

        // @media(max-width: 600px) {
        //     height: 148px;
        // }

        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }

        &:hover .news-archive__btn:before {
            width: 140px;

            @media(max-width: 1024px) {
                width: 105px;
            }
        }

        &:hover .news-archive__time {
            color: $purple-3;
        }
    }

    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        margin-left: -60px;

        @media(max-width: 600px) {
            margin-left: -30px;
        }

        @media(max-width: 500px) {
            margin-left: 0;
        }
    }

    &__caption {
        font-weight: bold;
        font-size: 25px;
        line-height: 30px;
        letter-spacing: -0.2px;
        font-family: 'MinionPro', sans-serif;
        color: $black;
        margin-top: 16px;
        cursor: normal;
    }

    &__bloc {
        padding-bottom: 26px;
        border-bottom: 1px solid $gray-3;

        @media(max-width: 1024px) {
            padding-bottom: 24px;
        }

        @media(max-width: 600px) {
            padding-bottom: 16px;
        }

        @media(max-width: 500px) {
            padding-bottom: 0;
            border-bottom: none;
        }

        &:not(:first-child) {
            margin-top: 30px;

            @media(max-width: 1024px) {
                margin-top: 24px;
            }
        }
    }

    .news-archive__cover {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
    }
}